@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 900px;
  margin: auto;

  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 16px;
  gap: 16px;

  font-family: "Montserrat", sans-serif;
}

.row {
  display: flex;
  flex-direction: row;
  grid-gap: 16px;
  gap: 16px;
}

.button-card {
  flex: 1 1;
  align-items: center;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  min-width: 304px;
}

.button-card__header {
  border-bottom: 1px solid #e0e0e0;
  padding-top: 8px;
  padding-bottom: 8px;
}

.button-card__content {
  align-items: center;
  padding: 16px 16px;
}

.footer {
  margin-top: 20px;
  margin-bottom: 40px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: #a9a9a9;
  font-size: 14px;
  line-height: 17px;
}

.footer__link {
  font-weight: 700;
  color: #a9a9a9;
}

@media only screen and (max-width: 768px) {
  .row {
    flex-direction: column;
  }
}

.button {
  border-radius: 6px;
  border: none;
  outline: none;

  box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
  cursor: pointer;

  display: flex;
  grid-gap: 8px;
  gap: 8px;
  align-items: center;
}

.button:hover,
.button:focus {
  background-color: #aeaeae;
}

.button--disable-shadow {
  box-shadow: none;
}

.button--color-default {
  background-color: #e0e0e0;
  color: #3f3f3f;
}

.button--color-primary {
  background-color: #3d5afe;
  color: white;
}

.button--color-primary:hover,
.button--color-primary:focus {
  background-color: #0039cb;
}

.button--color-secondary {
  background-color: #455a64;
  color: white;
}

.button--color-secondary:hover,
.button--color-secondary:focus {
  background-color: #1c313a;
}

.button--color-danger {
  background-color: #d32f2f;
  color: white;
}

.button--color-danger:hover,
.button--color-danger:focus {
  background-color: #9a0007;
}

.button--size-sm {
  padding: 6px 12px;
}

.button--size-md {
  padding: 8px 16px;
}

.button--size-lg {
  padding: 11px 22px;
}

.button--variant-outline {
  border: 1px solid #3d5afe;
  color: #3d5afe;
  background: none;
}

.button--variant-outline:hover,
.button--variant-outline:focus {
  background-color: rgba(41, 98, 255, 0.1);
}

.button--variant-text {
  background: none;
  color: #3d5afe;
}

.button--variant-text:hover,
.button--variant-text:focus {
  background-color: rgba(41, 98, 255, 0.1);
}

.button--disabled {
  color: #9e9e9e;
  background-color: #e0e0e0;
  cursor: not-allowed;
}
.button--disabled:hover,
.button--disabled:focus {
  background-color: #e0e0e0;
}

.button__label {
  flex: 1 1;

  font-family: "Noto Sans JP", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
}

